import ApiService from '@/services/ApiService'

export const namespaced = true

export const state = {
  universities: [],
  userUniversity: {}
}
export const mutations = {
  SET_UNIVERSITIES(state, universities) {
    state.universities = universities
  },
  SET_USER_UNIVERSITY(state, userUniversity) {
    state.userUniversity = userUniversity
  }
}
export const actions = {
  async fetchUniversities({ commit }) {
    const request = {
      method: 'get',
      url: '/university/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_UNIVERSITIES', response.data)
    } catch (error) {
      // await this.fetchUniversities({ commit })
      throw new error()
    }
  },
  async fetchUserUniversity({ commit }, id) {
    const request = {
      method: 'get',
      url: '/user/' + id,
      data: id
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_USER_UNIVERSITY', response.data)
    } catch (error) {
      // await this.fetchUserUniversity({ commit }, id)
      throw new error()
    }
  }
}
export const getters = {}
