import ApiService from '@/services/ApiService'
export const namespaced = true

export const state = {
  booksInUse: [],
  book: {},
  books: []
}
export const mutations = {
  SET_BOOKS_IN_USE(state, booksInUse) {
    state.booksInUse = booksInUse
  },
  SET_BOOKS(state, books) {
    state.books = books
  },
  SET_BOOK(state, book) {
    state.book = book
  }
}
export const actions = {
  async fetchBooks({ commit }) {
    const request = {
      method: 'get',
      url: '/books/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOKS', response.data)
      // await this.fetchBooks({ commit })
    } catch (error) {
      // await this.fetchBooks({ commit })
      throw new error()
    }
    // return ApiService.customRequest(request)
    //   .then(response => {
    //     commit('SET_BOOKS', response.data)
    //   })
    //   .catch(error => {
    //     throw error
    //   })
  },
  async fetchBooksInUse({ commit }) {
    const request = {
      method: 'get',
      url: '/book_in_use/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOKS_IN_USE', response.data)
      // await this.fetchBooksInUse({ commit })
    } catch (error) {
      // await ApiService.customRequest(request)
      throw new error()
    }
  },
  async fetchBook({ commit }, id) {
    const request = {
      method: 'get',
      url: '/books/' + id,
      data: id
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOK', response.data)
      // await this.fetchBook({ commit }, id)
    } catch (error) {
      // await this.fetchBook({ commit }, id)
      throw new error()
    }
  },
  async updateBookInUse({ dispatch }, { id, book }) {
    const request = {
      method: 'put',
      url: '/book_in_use/' + id,
      data: book
    }
    try {
      await ApiService.customRequest(request)
      await dispatch('fetchBooksInUse')
    } catch (error) {
      // await this.updateBookInUse({ dispatch }, { id, book })
      throw new error()
    }
  }
}
export const getter = {}
