<template>
  <v-app-bar app color="#f2dda8" dark height="80">
    <v-btn to="/" icon class="mr-6 ml-3">
      <v-avatar tile size="96">
        <v-img contain src="@/assets/white_logo.png" />
      </v-avatar>
    </v-btn>
    <v-spacer />
    <p
      style="color: #4a4a4a;"
      v-if="userUniversity"
      class="text-center mb-0 hidden-sm-and-down text-h6"
    >
      {{ userUniversity.name }}
    </p>
    <v-spacer />
    <v-btn text color="#4a4a4a" v-if="isAuth" @click="logout" rounded>
      Выйти</v-btn
    >
    <v-btn v-else text color="#4a4a4a" rounded to="/login">Вход</v-btn>
  </v-app-bar>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'TheAppBar',
  created() {
    if (this.isAuth) {
      this.$store.dispatch('university/fetchUniversities')
      this.$store.dispatch(
        'university/fetchUserUniversity',
        this.$store.state.auth.userId
      )
    }
  },
  computed: {
    ...mapState(['university']),
    isAuth() {
      return this.$store.state.auth.user
    },
    userUniversity() {
      return this.university.universities.find(university => {
        return university.id === this.university.userUniversity.university_id
      })
    }
  },
  methods: {
    logout() {
      this.$store.dispatch('auth/logout').then(() => {
        this.$router.push({ name: 'login' })
      })
    }
  }
}
</script>

<style scoped></style>
