import ApiService from '@/services/ApiService'
export const namespaced = true

export const state = {
  statsPerDay: {},
  statsPerMonth: {},
  statsPerWeek: {},
  statsPerYear: {},
  isLoaded: false
}

export const mutations = {
  SET_STATS_PER_DAY(state, statsPerDay) {
    state.statsPerDay = statsPerDay
  },
  SET_STATS_PER_WEEK(state, statsPerWeek) {
    state.statsPerWeek = statsPerWeek
  },
  SET_STATS_PER_MONTH(state, statsPerMonth) {
    state.statsPerMonth = statsPerMonth
  },
  SET_STATS_PER_YEAR(state, statsPerYear) {
    state.statsPerYear = statsPerYear
  }
}

export const actions = {
  async fetchStatsPerDay({ commit }) {
    const request = {
      method: 'get',
      url: '/stats_per_day/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_STATS_PER_DAY', response.data)
    } catch (error) {
      throw new error()
    }
  },
  fetchStatsPerWeek({ commit }) {
    const request = {
      method: 'get',
      url: '/stats_per_week/'
    }
    return ApiService.customRequest(request)
      .then(response => {
        commit('SET_STATS_PER_WEEK', response.data)
      })
      .finally(() => {
        state.isLoaded = true
      })
      .catch(error => {
        throw error
      })
  },
  fetchStatsPerMonth({ commit }) {
    const request = {
      method: 'get',
      url: '/stats_per_month/'
    }
    return ApiService.customRequest(request)
      .then(response => {
        commit('SET_STATS_PER_MONTH', response.data)
      })
      .finally(() => {
        state.isLoaded = true
      })
      .catch(error => {
        throw error
      })
  },
  fetchStatsPerYear({ commit }) {
    const request = {
      method: 'get',
      url: '/stats_per_year/'
    }
    return ApiService.customRequest(request)
      .then(response => {
        commit('SET_STATS_PER_YEAR', response.data)
      })
      .finally(() => {
        state.isLoaded = true
      })
      .catch(error => {
        throw error
      })
  }
}

export const getters = {}
