import Vue from 'vue'
import VueRouter from 'vue-router'
import TokenService from '@/services/TokenService'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'active-orders',
    component: () => import('@/views/ActiveOrders.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/books-in-use',
    name: 'books-in-use',
    component: () => import('@/views/BooksInUse.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/book-list',
    name: 'book-list',
    component: () => import('@/views/BookList.vue')
  },
  {
    path: '/book-show/:id',
    name: 'book-show',
    component: () => import('@/views/BookShow.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/statistics',
    name: 'statistics',
    component: () => import('@/views/Statistics.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/orders-list',
    name: 'orders-list',
    component: () => import('@/views/OrdersList.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/Login.vue')
  },
  {
    path: '/404',
    name: 'not-found',
    component: () => import('@/views/NotFound.vue')
  },
  {
    path: '*',
    redirect: { name: 'not-found' }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const loggedIn = TokenService.getToken('access')
    if (loggedIn) {
      next()
      return
    }
    next('/login')
  } else {
    next()
  }
})

export default router
