import ApiService from '@/services/ApiService'

export const namespaced = true

export const state = {
  orders: [],
  activeOrders: [],
  order: {}
}

export const mutations = {
  SET_ORDERS(state, orders) {
    state.orders = orders
  },
  SET_ORDER(state, order) {
    state.order = order
  },
  SET_ACTIVE_ORDERS(state, activeOrders) {
    state.activeOrders = activeOrders
  }
}

export const actions = {
  async fetchOrders({ commit }) {
    const request = {
      method: 'get',
      url: '/orders/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_ORDERS', response.data)
      // await this.fetchOrders({ commit })
    } catch (error) {
      // await this.fetchOrders({ commit })
      throw new error()
    }
    // return ApiService.customRequest(request)
    //   .then(response => {
    //     commit('SET_ORDERS', response.data)
    //   })
    //   .catch(error => {
    //     throw error
    //   })
  },
  async fetchActiveOrders({ commit }) {
    const request = {
      method: 'get',
      url: '/active_orders/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_ACTIVE_ORDERS', response.data)
      // await this.fetchActiveOrders({ commit })
    } catch (error) {
      // await ApiService.customRequest(request)
      throw new error()
    }
  },
  async updateActiveOrder({ dispatch }, { id, order }) {
    const request = {
      method: 'put',
      url: '/active_orders/' + id,
      data: order
    }
    try {
      await ApiService.customRequest(request)
      await dispatch('fetchActiveOrders')
    } catch (error) {
      await this.updateActiveOrder({ dispatch }, { id, order })
      // await dispatch('fetchActiveOrders')
      throw new error()
    }
  }
}

export const getters = {}
