import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import ApiService from '@/services/ApiService'
import 'nprogress/nprogress.css'
import TokenService from '@/services/TokenService'
import jwtDecode from 'jwt-decode'

Vue.config.productionTip = false

ApiService.init('https://autolib.tdtu.uz/api/v1/admin')
let access = TokenService.getToken('access')
if (access) {
  store.commit('auth/SET_AUTH_USER', access)
  const userId = jwtDecode(access).user_id
  store.commit('auth/SET_USER_ID', userId)
  ApiService.setHeader('Authorization', `Bearer ${access}`)
}
ApiService.mountInterceptor()

new Vue({
  router,
  store,
  vuetify,
  created() {
    this.timeout = setInterval(
      () =>
        this.$store.dispatch('auth/refreshToken', {
          refresh: TokenService.getToken('refresh')
        }),
      270000
    )
  },
  render: h => h(App)
}).$mount('#app')
